import Nickname from "@/components/actionbar/Nickname.vue"
export default{
	data(){
		return {
			// CustomBar: this.CustomBar,
			modalName: null,
			value:"",
			imgList: []
		}
	},
	components:{
		Nickname,
	},
	methods:{
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		Modify(){
			this.value = this.value
			this.modalName = null
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {
			uni.showModal({
				title: '亲!!!',
				content: '确定要删除这个头像吗？',
				cancelText: '再看看',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		ChooseImage() {
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					console.log(res)
					// console.log(res.tempFiles[0].path)
					//tempFiles
					if (this.imgList.length != 0) {
						this.imgList = this.imgList.concat(res.tempFiles)
						console.log(this.imgList)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		Confirmupload(){
			this.imgList = this.imgList
			this.modalName = null
			console.log(this.imgList)
			// uni.uploadFile({
			// 	url:"http://hbk.huiboke.com/api/common/uploadImage?type=user",
			// 	filePath:this.imgList,
			// 	key:"file",
			// 	success(res){
			// 		console.log(res)
			// 	},
			// 	fail(err){
			// 		console.log(err)
			// 	}
			// })
		}
	}
}
